import React from 'react'
import {FaRegWindowClose,
    FaRegCheckSquare,
    } from "react-icons/fa";
    const LotCardComponent = ({ sl,el, users, handleSoldstatus, formatTime, time ,index}) => (
        <div className="col-md-3 col-sm-12 col-xs-12 list">
          <div className="card mb-4">
            <div className="badge">Lot- {el.lot}</div>
            <div className="red-digit"> {sl}</div> {/* Add a red digit here */}
              
            <div className="card-body ">
              <span className="text-white bg-l px-2 py-1 rounded-pill animate__animated animate__flash animate__infinite">
                Live {formatTime(time)}
                <span className="dot"></span>
              </span>
      
              <div className="mt-3 row">
                <div className="col-md-12 col-sm-12 col-xs-12 ">
                  <h5 className="card-title">{el.factory_name}</h5>
                  <p className="card-text">INV:{el.invoice} LOT:{el.lot}</p>
      
                  <p className="card-text">
                    PRICE:{el.price} QTY:{el.pkgs}
                  </p>
                  <p className="card-text">GRADE:{el.grade}</p>
      
                  <div className="card-sp">
                    {el.maximum_bid === 0 ? (
                      <>
                      </>
                    ) : (
                      <>
                        <h5 className="card-price">Max.{el.maximum_bid}</h5>
                        <span
                          className={
                            el.buyer === users.link_id ? "hname" : "rname"
                          }
                        >
                          {el.buyer_name}({el.maximum_bid - el.price})
                        </span>
                        <br />
                      </>
                    )}
                  </div>
                </div>
      
                {/* <div className="col-md-5 d-flex justify-content-center align-items-center col-sm-5 col-xs-5 b-bg">
                  <div className="cusrow">
                    <button
                      onClick={() => handleSoldstatus(el, 1)}
                      disabled={el.brokers === users.link_id ? false : true}
                      className="btn btn-success square-circle mr-4"
                    >
                      <FaRegCheckSquare className="bid" />
                    </button>
                    <button
                      onClick={() => handleSoldstatus(el, 2)}
                      disabled={el.brokers === users.link_id ? false : true}
                      className="btn btn-danger square-circle"
                    >
                      <FaRegWindowClose className="bid" />
                    </button>
                  </div>
                </div> */}
              </div>
            </div>
          </div>
        </div>
      );
      

export default LotCardComponent
